.contact-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.contact-title-div {
  margin-top: 11vh;
  margin-left: 20%;
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: flex-start;
}

.contact-title {
  font-size: 2.5rem;
  color: var(--secondary-color);
  text-align: start;
  font-weight: 700;
  letter-spacing: 1px;
}

.contact-seperator {
  margin-top: -30px;
  width: 70%;
  border-bottom: var(--secondary-color) 2px solid;
}

.contact-suptitle {
  margin-top: 10px;
  font-size: 1.3rem;
  color: var(--text-color);
}

.contact-info-container {
  margin-top: 8vh;
  display: flex;
  flex-direction: row;
  width: 50%;
  height: auto;
  justify-content: space-evenly;
  align-self: center;
  background-color: var(--box-bg-color);
  border-radius: 40px;
  -webkit-box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
  box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
  padding: 40px;
}

.contact-info-div {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: auto;
  align-items: flex-start;
  justify-content: flex-start;
}

.contact-info-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.contact-icon {
  font-size: 1.3rem;
  margin-right: 10px;
  color: var(--secondary-color);
}

.contact-text {
  margin-top: 20px;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-color);
}

.contact-approach-text {
  font-size: 1rem;
  font-weight: 600;
  margin-left: 30px;
  margin-top: -5px;
  color: var(--text-color);
}

.contact-text-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--secondary-color);
}
.contact-approach-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--secondary-color);
}

.contact-button {
  background-color: var(--secondary-color);
  color: var(--primary-color);
  border-radius: 40px;
  border: none;
  width: 150px;
  height: 30px;
  font-size: 1rem;
  font-weight: 500;
  -webkit-box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
  box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
  outline: none;
  transition: 500ms;
}

.contact-button:hover {
  opacity: 0.8;
  transition: 500ms;
}
@media only screen and (max-width: 600px) {
  .contact-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  .contact-title-div {
    margin-top: 11vh;
    margin-left: 5%;
    display: flex;
    flex-direction: column;
    width: 90%;
    align-items: flex-start;
  }

  .contact-title {
    font-size: 1.8rem;
    color: var(--secondary-color);
    text-align: start;
    font-weight: 700;
    letter-spacing: 1px;
  }

  .contact-seperator {
    margin-top: -20px;
    width: 90%;
    border-bottom: var(--secondary-color) 2px solid;
  }

  .contact-suptitle {
    margin-top: 10px;
    font-size: 1.2rem;
    font-weight: 600;
    color: var(--text-color);
  }

  .contact-info-container {
    margin-top: 5vh;
    margin-bottom: 5vh;
    display: flex;
    flex-direction: column;
    width: 70%;
    height: auto;
    justify-content: space-evenly;
    align-self: center;
    background-color: var(--box-bg-color);
    border-radius: 40px;
    -webkit-box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
    box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
    padding: 40px;
  }

  .contact-info-div {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    align-items: flex-start;
    justify-content: flex-start;
  }

  .contact-info-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }

  .contact-icon {
    font-size: 1.3rem;
    margin-right: 10px;
    color: var(--secondary-color);
  }

  .contact-text {
    margin-top: 20px;
    font-size: 1rem;
    font-weight: 600;
    color: var(--text-color);
  }

  .contact-approach-text {
    font-size: 1rem;
    font-weight: 600;
    margin-left: 30px;
    margin-top: -5px;
    color: var(--text-color);
  }

  .contact-text-title {
    font-size: 1.3rem;
    font-weight: 700;
    color: var(--secondary-color);
  }
  .contact-approach-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--secondary-color);
  }

  .contact-button {
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-radius: 40px;
    border: none;
    width: 200px;
    height: 40px;
    font-size: 1rem;
    font-weight: 500;
    -webkit-box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
    box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
    outline: none;
    transition: 500ms;
  }
}
