.trainer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.trainer-profile-container {
  margin-top: 150px;
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 60%;
  background-color: var(--box-bg-color);
  padding: 40px;
  border-radius: 40px;
  margin-bottom: 100px;
  -webkit-box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
  box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
}

.trainer-text-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-flex-;
  align-items: flex-start;
  text-align: justify;
}

.trainer-name {
  font-size: 1.8rem;
  color: var(--secondary-color);
  text-align: start;
  font-weight: 700;
}

.trainer-text {
  font-size: 1.2rem;
  color: var(--text-color);
  text-align: justify;
}

.trainer-container img {
  height: 50vh;
  width: auto;
  margin-left: 50px;
  border-radius: 40px;
}

@media only screen and (max-width: 600px) {
  .trainer-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  .trainer-profile-container {
    margin-top: 150px;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 70%;
    background-color: var(--box-bg-color);
    padding: 40px;
    border-radius: 40px;
    margin-bottom: 100px;
    -webkit-box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
    box-shadow: 0px 30px 47px -30px rgba(138, 149, 158, 0.2);
  }

  .trainer-text-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: justify;
  }

  .trainer-name {
    font-size: 1.8rem;
    color: var(--secondary-color);
    text-align: start;
    font-weight: 700;
  }

  .trainer-text {
    font-size: 1.2rem;
    color: var(--text-color);
    text-align: justify;
  }

  .trainer-container img {
    height: 300px;
    width: auto;
    margin-top: 10px;
    border-radius: 40px;
    margin-left: 50%;
    margin-right: 50%;
  }
}
