.navbar-div {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  background-color: transparent;
  color: var(--primary-color);
  top: 0;
  z-index: 10;
}

.navbar-div img {
  height: auto;
  width: 250px;
  justify-self: start;
  margin-left: 10px;
}

.navbar-nav-div {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  justify-items: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 70px;
}

.navbar-icon {
  font-size: 2rem;
  color: var(--text-color);
  margin-right: 30px;
  transition: 1s;
}

.navbar-icon.active {
  transform: rotate(90deg);
  transition: 1s;
}

.navbar-sidebar {
  flex-direction: column;
  border-radius: 0px;
  background-color: var(--primary-color);
  width: 300px;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  right: -100%;
  transition: 850ms;
  align-items: flex-start;
  overflow: hidden;
  z-index: 10;
}

.navbar-sidebar.active {
  right: 0;
  transition: 350ms;
}

.navbar-icon-close {
  align-items: center;
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 40px;
  font-size: 30px;
  color: var(--text-color);
  justify-content: start;
  font-weight: 700;
}

.navbar-icon-close:hover {
  color: var(--secondary-color);
  transition: 500ms;
}

.navbar-link {
  text-decoration: none;
  font-size: 1.2rem;
  color: var(--text-color);
  padding: 10px 10px 10px 20px;
  font-weight: 700;
}
.navbar-link:hover {
  color: var(--secondary-color);
}

@media only screen and (max-width: 600px) {
  .navbar-div {
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    background-color: transparent;
    color: var(--primary-color);
    top: 0;
    z-index: 10;
  }

  .navbar-sidebar {
    display: flex;
    flex-direction: column;
    border-radius: 0px;
    background-color: var(--primary-color);
    width: 300px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    align-items: flex-start;
    overflow: hidden;
    z-index: 10;
  }

  .navbar-sidebar.active {
    left: 0;
    transition: 350ms;
  }

  .navbar-div img {
    height: auto;
    width: 200px;
    justify-self: start;
    margin-left: 10px;
  }
}
