footer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50px;
  background-color: transparent;
  bottom: 0;
  justify-content: center;
  align-items: center;
  border-top: #535353cc 1px solid;
}
.footer-div {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50px;
  justify-content: space-evenly;
  align-items: center;
}
.footer-link {
  font-size: 1rem;
  text-decoration: none;
  color: var(--text-color);
  transition: 500ms;
}

.footer-link:hover {
  text-decoration: none;
  color: var(--secondary-color);
  transition: 500ms;
}

.footer-creator-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.footer-icon {
  margin-top: 3px;
  font-size: 1.3rem;
  margin-right: 5px;
  color: var(--text-color);
}

.footer-text {
  font-size: 1rem;
  color: var(--text-color);
}

@media only screen and (max-width: 600px) {
  .footer-link {
    font-size: 0.6rem;
    text-decoration: none;
    color: var(--text-color);
    transition: 500ms;
  }

  .footer-icon {
    margin-top: 1px;
    font-size: 0.8rem;
    margin-right: 1px;
    color: var(--text-color);
  }

  .footer-text {
    font-size: 0.6rem;
    color: var(--text-color);
  }
}
